@import url("https://fonts.googleapis.com/css2?family=Lobster&display=swap");

.header-calendar {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.month-calendar {
  font-size: 46px;
  letter-spacing: 5px;
  text-transform: uppercase;
  font-family: "Lobster", cursive;
  margin: 0;
}
